import './graphisme.style.scss'
import blueArrow from '../../../assets/img/ArrowBleu.svg'
import greyGrid from '../../../assets/img/BG-PG.svg'
import blueGrid from '../../../assets/img/BG-PB.svg'

import SubNavHeader from '../../../components/shared/sub-nav-header/sub-nav-header.component'
import headerImg from '../../../assets/img/desktop.png'
import Footer from '../../../components/footer/footer.component'


const Graphisme = () => {
    return (
        <div className='graphisme'>
            <SubNavHeader 
            title="Notre <span className='underline'>crativit au services</span>
            <br>de <span className='blue'>votre image.</span>"
            citation='Le design graphique est un lment essentiel pour russir et se <br/> dmarquer sur le march en ligne.'
            subtitle="Identit visuelle"
            subcontent="L'identit visuelle reprsentent votre entreprise, le logo, les couleurs, les typographies et les icnes. Chez GraphiLeaf, nous crons une <strong>identit visuelle unique</strong> pour votre entreprise qui vous permettra de vous dmarquer de la concurrence et de renforcer la reconnaissance de votre marque."
            img={headerImg}
            />


            <section className='sub-part'>
                <div className='title-container'>
                    <h1>Supports de communication</h1>
                    <img src={blueArrow} className='blueArrow' alt='blueArrow'/>
                </div>
                <p className='content'>
                    Nous ralisons tous les supports de communication ncessaires pour votre entreprise, tels que les cartes de visite, les flyers, les brochures et les affiches. Nous concevons des supports de qualit professionnelle qui <strong>renforcent l'image de votre entreprise</strong> et vous permettent de <strong>communiquer efficacement</strong> avec votre public.
                </p>
                <img className='grey-grid' src={greyGrid} alt='grey grid'/>
            </section>
            
            <section className='description-container'>
                <div className='left'>
                    <div className='title-container'>
                        <h1>Design Web</h1>
                        <img src={blueArrow} className='blueArrow' alt='blueArrow'/>
                    </div>
                    <p className='description'>
                    Nous proposons des services de design web afin d'amliorer l'apparence de votre site web. Nous crons des designs modernes, purs et <strong>adapts  votre identit</strong> visuelle pour une exprience utilisateur optimale. Nous pouvons galement concevoir des pages de destination (landing pages), des maquettes de site web ainsi que des bannires publicitaires.
                    </p>

                </div>
                <div className='right'>
                    <div className='title-container'>
                        <h1>Retouche d'images</h1>
                        <img src={blueArrow} className='blueArrow' alt='blueArrow'/>
                    </div>
                    <p className='description'>
                        Nous proposons galement des services de retouche d'images pour amliorer la qualit de vos visuels existants ou pour les adapter  <strong>vos besoins.</strong> Que ce soit pour des photos de produits, des portraits professionnels ou des images de votre entreprise, nous pouvons amliorer leur qualit pour une prsentation professionnelle.
                    </p>
                </div>
                <img className='blueGrid' src={blueGrid} alt='blue grid'/>
            </section>
            <Footer />
        </div>
    )
}

export default Graphisme